body {
  background-color: green;
  margin: 0 auto;
  
}

.eventBox {
  background-color: yellow;
  border: 2px solid black;
  width: 80vw;
  height: 35vh;
  margin: 0;
  padding: 20px;
  text-align: center;
  border-radius: 4px;
}
.eventBox:hover {
  background-color: yellowgreen;
  -webkit-box-shadow: 5px 10px;
          box-shadow: 5px 10px;
  -webkit-transform: translate(5px, 5px) scale(1.01);
      -ms-transform: translate(5px, 5px) scale(1.01);
          transform: translate(5px, 5px) scale(1.01);
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
	-webkit-box-shadow: 0px 0px 25px 0px #4b0175;
	        box-shadow: 0px 0px 25px 0px #4b0175;
}
button{
  cursor: pointer;
}


